@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono&family=Sora&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0px;margin:0px;box-sizing:border-box}.page-wrapper{font-family:"Sora",sans-serif;background-color:#313131;color:#fff;height:100vh;display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;justify-content:center;align-items:center}.page-wrapper .title{margin:0px;font-size:2rem;font-weight:600}.page-wrapper .image{width:8rem}.page-wrapper .break-message{text-align:center;margin-bottom:1rem}.page-wrapper #timer-label{text-align:center}.page-wrapper .timer{text-align:center}.page-wrapper .play-pause{margin:1rem}.length-container{display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:.5rem;gap:.5rem}.length-container .time-boxes{display:flex;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem}
